import React from 'react';
import {
  Blitz,
  Grazio,
  Delight,
  Intelio,
  Geneo
} from '../../utils/prodict-specification.jsx'
import {
  BlitzDescription,
  GrazioDescription,
  DelightDescription,
  IntelioDescription,
  GeneoDescription
} from '../../utils/product-description.jsx'

import './product.scss'
import BasicTabs from "./product-tabs/product-tabs.jsx";

const Product = () => {

  const guarantee = [
    {
      'period': ' 1  ',
      'note' : '   '
    },
    {
      'period': ' 1  ',
      'note' : '  '
    },
    {
      'period': ' 2  ',
      'note' : ' '
    },
    {
      'period': ' 5  ',
      'note' : '  (40   )'
    },
    {
      'period': '5  ',
      'note' : '  '
    },
    {
      'period': '5  ',
      'note' : '  '
    },
    {
      'period': '1  ',
      'note' : '   '
    },
  ]

  return (
    <div className='product'>
      <div className="wrap">
        <h2 className="product-title"></h2>
        <p className='product-subtitle'>, &nbsp;&nbsp;,  &nbsp;&nbsp;  </p>
        <BasicTabs manufacturer={{
          Blitz,
          Grazio,
          Delight,
          Intelio,
          Geneo
        }}
        description={{
         BlitzDescription,
         GrazioDescription,
         DelightDescription,
         IntelioDescription,
         GeneoDescription
        }}
        />
      </div>
      <div className="product-guarantee">
        <div className="wrap">
          <h2 className="product-guarantee__title"></h2>
          <h5 className="product-guarantee__subtitle">      ""       :</h5>
          <div className="product-guarantee__wrap">
          {
            guarantee.map((guarantee) => {
              return (
                  <div className="product-guarantee__item">
                    <span>{guarantee.period}</span>
                    {guarantee.note}
                  </div>
              )
            })
          }
          </div>
        </div>
      </div>
    </div>
  );
};

export default Product;